<template>
	<div>
		<template v-if="loadingActions">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Actions</h3>
				</div>

				<div class="d-flex">
					<button class="btn btn__primary" type="button" name="button"
						data-toggle="modal" data-target="#categoryModal">
						<span class="btn__icon">
							<svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z" fill="#fff"/></svg>
						</span>Add Action</button>
				</div>
			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Title</p>
						<p>Link</p>
						<p>Actions</p>
					</div>
					<div class="contacts__body">
						<template v-if="actionsLoaded && actions.length">
							<ActionItem
								v-for="action in actions"
								:key="action.id"
								:action="action">
								&nbsp;
							</ActionItem>
						</template>
						<template v-else-if="actionsLoaded">
							<p>No action found</p>
						</template>
						<template v-else>
							<p>An error occurred while loading actions, please try again.</p>
						</template>
					</div>
				</div>
			</section>
		</template>
		<Pagination
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit" />
		<AddAction />
	</div>
</template>

<script>
import Pagination from '../../components/Pagination';
import Loader from '../../components/Loader';
import ActionItem from './ActionItem';
import AddAction from './AddAction';
import { getActions } from '../../utils/api/action';
import eventHub from '../../utils/eventhub';

export default {
	name: 'Index',
	data() {
		return {
			loadingActions: true,
			actions: {},
			actionsLoaded: false,
			limit: 10,
			offset: 0,
			total: 0,
			baseUrl: 'actions'
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getActions();
		eventHub.$on('action-created', (payload) => {
			this.actions.unshift(payload);
		});
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getActions();
		}
	},
	components: {
		Loader,
		ActionItem,
		Pagination,
		AddAction
	},
	methods: {
		getActions() {
			this.loadingActions = true;
			getActions(this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.actions = response.data.data;
						this.total = response.data.total;
						this.actionsLoaded = true;
					}
				})
				.catch((error) => {
					this.loadingActions = false;
					this.actionsLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingActions = false;
				});
			return true;
		}
	}
};
</script>

<style lang="scss">
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.contacts--list__item {
	display: grid;
	grid-template-columns: 35px 1fr 1fr 150px;
}
</style>
