<template>
	<div class="modal fade" id="categoryModal" tabindex="-1" role="dialog" aria-labelledby="categoryModalTitle" aria-hidden="true">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="categoryModalTitle">Add New Action</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form 
						@submit.prevent="createAction"
						class="modal__form" 
						method="post">
						<div class="form-group">
							<label class="modal__label" for="title">Title</label>
							<input 
								type="text" 
								:class="[{'is-invalid': errors.name}]"
								class="form-control" 
								id="title" 
								placeholder="Action Title"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.title">
							<div class="invalid-feedback">
								{{ errors.title }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="link">Link</label>
							<input 
								type="text" 
								:class="[{'is-invalid': errors.name}]"
								class="form-control" 
								id="link" 
								placeholder="Action Link"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.link">
							<div class="invalid-feedback">
								{{ errors.link }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button 
						@click="clearErrors()"
						class="btn btn--md" 
						type="button" 
						name="button">Cancel</button>
					<button 
						@click.prevent="createAction"
						class="btn btn--md btn__primary" 
						type="submit" 
						name="button">
						Save Action</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import form from '../../mixins/form';
import Loader from '../../components/Loader';
import { createAction } from '../../utils/api/action';
import action from '../../utils/validations/action';
import { success, fail } from '../../utils/toast';
import eventHub from '../../utils/eventhub';

export default {
	name: 'AddAction',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loading: false
		};
	},
	components: {
		Loader
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	methods: {
		createAction() {
			this.validate(this.itemData, action.create);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createAction(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.itemData = {};
						this.loading = false;
						eventHub.$emit('action-created', response.data);
						success({
							title: 'Success! A new action has been created'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong'
					});
					return error;
				});
			return true;
		}
	}
};
</script>
